import React, { useState, useEffect } from "react";  
import "./navbar.css";
import { Link } from "react-router-dom";

//  
import englishFlag from "../assets/english-flag.png";
import arabicFlag from "../assets/arabic-flag.png";
import profilePic from "../assets/profile.png"; 

const Navbar = () => {
  const [language, setLanguage] = useState("EN");
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [user, setUser] = useState(null); 
  const [searchQuery, setSearchQuery] = useState("");
  const [searchResults, setSearchResults] = useState([]);

  //  
  const handleLanguageChange = (lang) => {
    setLanguage(lang);
    console.log("Language changed to:", lang);
  };

  //   
  const handleSearchChange = async (e) => {
    const query = e.target.value;
    setSearchQuery(query);

    if (query.length > 2) {
      try {
        const response = await fetch(`https://api.example.com/search?q=${query}`);
        const data = await response.json();
        setSearchResults(data.results); 
      } catch (error) {
        console.error("Error fetching search results:", error);
      }
    } else {
      setSearchResults([]); //        3 
    }
  };

  //       
  useEffect(() => {
    const loggedInUser = JSON.parse(localStorage.getItem("user")); //     localStorage
    if (loggedInUser) {
      setIsLoggedIn(true);
      setUser(loggedInUser); 
    }
  }, []);

  //   
  const handleLogout = () => {
    localStorage.removeItem("user"); //     log out
    setIsLoggedIn(false);
    setUser(null);
  };

  return (
    <nav className="navbar">
      <div className="navbar-left">
        <div className="navbar-logo">
          <Link to="/"> 
            <img src="/logo.png" alt="Logo" />
          </Link>
        </div>

        {/*   */}
        <div className="search-container">
          <input
            type="text"
            placeholder="Search..."
            value={searchQuery}
            onChange={handleSearchChange}
            className="search-input"
          />
  
          {searchQuery && searchResults.length > 0 && (
            <div className="search-results">
              <ul>
                {searchResults.map((result, index) => (
                  <li key={index}>
                    <Link to={`/search/${result.id}`}>{result.name}</Link>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>
      </div>

      <div className="navbar-right">
        {isLoggedIn ? (
          <div className="profile-container">
            <img
              src={user?.profilePicture || profilePic} //   
              alt="Profile"
              className="profile-pic"
            />
            <button onClick={handleLogout} className="btn logout-btn">
              Logout
            </button>
          </div>
        ) : (
          <>
            <Link to="/login" className="btn login-btn">Login</Link>
            <Link to="/signup" className="btn signup-btn">Sign Up</Link>
          </>
        )}

        {/*    */}
        <div className="language-dropdown">
          <button className="btn language-btn">
            {language === "EN" ? (
              <>
                <img src={englishFlag} alt="English" className="flag-icon" />{" "}
                English
              </>
            ) : (
              <>
                <img src={arabicFlag} alt="Arabic" className="flag-icon" />{" "}
                
              </>
            )}
          </button>
          <div className="dropdown-menu">
            <span onClick={() => handleLanguageChange("EN")}>
              <img src={englishFlag} alt="English" className="flag-icon" /> English
            </span>
            <span onClick={() => handleLanguageChange("AR")}>
              <img src={arabicFlag} alt="Arabic" className="flag-icon" /> 
            </span>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
